import config from './config';
import React from 'react';
import {render} from 'react-dom';

const {namespace} = config;
const hasOwn = {}.hasOwnProperty;

/**
 *  className
 * @param  {string|object} args  className
 *  className  namespace 
 *  class  true  class 
 *  `_user` 
 * @return {string}          class"class1 class2 class3"
 *
 * @example
 * let classes = classNames('dialog', {
 *   'dialog-btn': false,
 *   'dialog-title': true,
 *   '_user': 'user-class'
 * });
 * console.log(classes);
 * //  namespace='rd'
 * // output: "rc-dialog rc-dialog-title user-class"
 */
export function classNames(...args) {
  let classes = [];
  let arg, argType, key;

  for (let i = 0, len = args.length; i < len; i++) {
    arg = args[i];
    argType = typeof arg;

    if (!arg) continue;

    if (argType === 'string' || argType === 'number') {
      prePush(classes, arg);
    } else if (Array.isArray(arg)) {
      classes.push(classNames.apply(null, arg));
    } else if (argType === 'object') {
      for (key in arg) {
        if (!hasOwn.call(arg, key) || !arg[key]) continue;

        // 
        if (key === '_user') {
          classes.push(arg[key]);
        } else {
          prePush(classes, key);
        }
      }
    }
  }

  return classes.join(' ');
}

function prePush(arr, value) {
  value = `${namespace}-${value}`;
  return arr.push(value);
}


/**
 *  Component  container
 * @param  {Component} Component React 
 * @param  {node} container  dom 
 *  dom 
 * @return {PropTypes.element}           Component 
 */
export function createInstance(Component, container) {
  if (!container) {
    container = createContainer();
  }

  return render(<Component />, container);
}

/**
 *  dom div.${namespace}-apicontainer
 * @return {dom} 
 */
function createContainer() {
  let div = document.createElement('div');
  div.className = classNames('api-container');
  document.body.appendChild(div);

  return div;
}





